import React from 'react';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom';
import { Home, About, NotFound, Login } from './pages';
import { PrivateRoute } from './middlewares';

const isLogged = false;

const App = () => {
  return (
    <BrowserRouter>
      <header>
        <h1>Meu site legal</h1>
        <nav>
          <ul>
            <li><Link to="/react-reduxintroduction">Home</Link></li>
            <li><Link to="/react-reduxintroduction/about">Sobre</Link></li>
            <li><Link to="/react-reduxintroduction/iqhy2i8hsiduahsdas">Teste 404</Link></li>
          </ul>
        </nav>
      </header>

      <hr />

      <Switch>
        <Route exact path="/react-reduxintroduction">
          <Home />
        </Route>

        <Route path="/react-reduxintroduction/login">
          <Login />
        </Route>

        <PrivateRoute isLogged={isLogged} path="/react-reduxintroduction/about">
          <About />
        </PrivateRoute>

        <Route path="*">
          <NotFound />
        </Route>
      </Switch>

      <hr />

      <footer>
        Todos os direitos reservados...
      </footer>
    </BrowserRouter>
  );
};

export default App;
